* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .nav-bar {
    width: 100%;
    height: 50px;
    background-color: #1a7a5d;
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
  }
  
  .search-bar{
    display: flex;
    justify-content: space-between;
    background-color: #e3e3e3;
    width: 35%;
    border-radius: 5rem;
    align-items: center;
    padding: 0 1%;
  }
  
#user-input {
    height: 35px;
    width: 100%;
    border-radius: 5rem;
    border: hidden;
    background-color: #e3e3e3;
    padding: 0 1%;
  }
  
  input:focus {
    outline: none;
    background-color: #e3e3e3;
  }
  
  input[type="submit"] {
    border: hidden;
    background-color: #e3e3e3;
    cursor: pointer;
    font-size: 20px;
  }
  
  .content-box {
    position: relative;
    top: 50px;
    width: 66%;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  
  header {
    padding-top: 2vh;
    padding-bottom: 1vh;
    text-align: center;
    font-family: sans-serif;
  }
  
  hr {
    background-color: #1a7a5d;
    height: 1px;
    border: none;
  }
  
  #albums {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .album {
    margin: 3px;
    border: 1px solid;
    display: inline-flex;
    flex-direction: column;
    background-color: #f3f3f3;
    width: 150px;
    height: 210px;
    box-sizing: border-box;
  }
  
  .cover-pic {
    padding: 0.5rem 0.5rem;
    cursor: pointer;
  }
  
  .cover-pic:hover {
    background-color: #c3c3c3;
    border-radius: 5px;
  }
  
  .collection-name {
    padding: 0.2rem 1rem 1rem;
    font-family: sans-serif;
    text-align: center;
    font-size: 12px;
    cursor: pointer;
  }
  
  .collection-name:hover {
    color: gray;
  }
  
  .loader {
    display: none;
    align-self: center;
    margin: 2vh 0 1vh;
    height: 25px;
    width: 25px;
  }
  
  #loadMore {
    position: relative;
    display: none;
    left: 50%;
    top: 50px;
    transform: translateX(-50%);
    width: 20px;
    height: 20px;
    border-radius: 10px;
    cursor: pointer;
    margin: 15px 0 80px;
  }
  
  #loadMore:hover {
    background-color: #b3b3b3;
  }